import React from 'react'
import '../styles/Footer.css'
import ubicacion from '../assets/ubicacion.svg'
const Footer = () => {
  return (
    <footer className='footer' tabIndex={0}>
        <section className='left_side'>
            <p className='mb-0'>Gobierno de la Repblica del Ecuador</p>
        </section>
        <section className='right_side'>
            <img className='ubicacion_logo' src={ubicacion} alt="cono de ubicacin" />
            <div className='info_container' aria-label='Informacin de contacto'>
                <p tabIndex={0}>Av. Repblica de el Salvador N36-64 y Suecia</p>
                <p tabIndex={0}>Cdigo Postal: 170135 / Quito - Ecuador</p>
                <p tabIndex={0}>Telfono: 593-2 3976000</p>
            </div>
        </section>
    </footer>
  )
}

export {Footer};